@import "./variables";

.page-header {
  padding-top: 26px;
  padding-bottom: 50px;
  background: $bgrGreen;
}

.all-top-page {
  margin: 5px;
}

.page-img {
  width: 329px;
  height: 100%;
}

.pagination {
  ul {
    display: flex;
  }

  li {
    span {
      text-align: center;
      padding-top: 55px;
      font-family: $fontTitles;
      font-style: normal;
      font-weight: 400;
      font-size: 18px;
      line-height: 62px;
      color: #FFFFFF;
      cursor: pointer;
      transition: all 0.5s;

      &:hover {
        color: $bgrLittle;
        transition: all 0.5s;
      }
    }

    &:nth-child(even) {
      margin: 0 15px 0;
    }
  }
}

.arrows {
  padding-top: 55px;
  display: flex;
  align-items: center;
  justify-content: center;

  svg {
    transition: all 0.5s;
    &:hover {
      path {
        fill: $bgrLittle;
        transition: all 0.5s;
      }
    }

  }

  h2 {
    padding-top: 0;
    flex: 1;
  }
}
@media screen and (max-width: 425px) {
  nav{
    display: none;
  }
}