@import "src/style/variables";


form {
  display: flex;
  flex-direction: column;
  align-items: center;

  .input__frm {
    display: flex;
    margin-top: 106px;
    justify-content: center;
  }

  .btn_mdl {
    margin-top: 44px;

    .btn_modal {
      position: relative;
      border: none;
      cursor: pointer;
      background: none;
      font-family: $fontTitles;
      font-style: normal;
      font-weight: 400;
      font-size: 24px;
      line-height: 17px;
      text-align: center;
      color: #D1AE7E;
      transition: all 0.5s;

      &:hover {
        transition: all 0.5s;
        color: rgba(209, 174, 126, 0.42);
      }

      &:disabled {
        color: rgba(209, 174, 126, 0.42);
      }

      &::before {
        position: absolute;
        content: '';
        width: 66px;
        height: 12px;
        top: 0;
        right: 0px;
        border: 1px solid $bgrLittle;
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
        border-radius: 50%;
      }

      &::after {
        position: absolute;
        content: '';
        width: 43px;
        top: 6px;
        right: -60px;
        height: 0;
        background: $bgrLittle;
        border: 1px solid $bgrLittle;
        border-top-right-radius: 5px;
        border-bottom-right-radius: 5px;

      }
    }
  }

  label {
    display: flex;
    flex-direction: column;
    align-items: flex-start;


    &:not(:last-child){
      margin-right:60px;
    }
  }

  input {
    width: 314px;
    height: 48px;
    padding-right: 15px;
    padding-left: 15px;
    background: #343736;
    border-radius: 50px;
    border: none;
    outline: none;
    //Text
    font-family: $fontSupTitles;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    color: rgba(255, 255, 255, 0.59);


    ::placeholder {
      font-family: $fontSupTitles;
      font-style: normal;
      font-weight: 400;
      font-size: 16px;
      line-height: 19px;
      color: rgba(255, 255, 255, 0.59);
    }
  }
  .swal-modal{
    background: #343736!important;
  }
  .swal-text{
    font-family: $fontTitles;
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 17px;
    text-align: center;
    color: #D1AE7E;

  }
}
@media screen and (max-width: 1024px) {
  form .input__frm {
    margin-top: 40px;
  }
  form label:not(:last-child) {
    margin-right: 20px;
  }
}

@media screen and (max-width: 768px) {
  form .input__frm {
    margin-top: 40px;
  }
  form label:not(:last-child) {
    margin-right: 20px;
  }
  form input {
    width: 274px;
    height: 48px;
  }
}
@media screen and (max-width: 425px) {
  form {
    .input__frm {
      margin-top: 10px;
    }

    label:not(:last-child) {
      margin: 10px 0;
    }

    input {
      width: 224px;
      height: 48px;
    }
       .btn_mdl{
         margin-top: 10px;
       }
  }
  .input__frm {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .modal-form{
    top: 80px;
  }
}
