@import "./variables";

.about {
  background: #343736;
  height: 575px;
  position: relative;
  overflow: hidden;



  .about_img {
    z-index: 3;
  }

  .about_backg {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;

    p {
      font-family: $fontSupTitles;
      font-style: normal;
      font-weight: 400;
      font-size: 128px;
      line-height: 120px;


      color: rgba(60, 77, 73, 0.31);
    }
  }


}

.about-me {
  display: flex;
  justify-content: center;
  align-items: center;


  .about-text {
    display: flex;
    flex-direction: column;
    align-items: center;

    p {
      margin-left: 149px;
      font-family: $fontSupTitles;
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      line-height: 22px;
      text-align: center;
      color: #FFFFFF;

      &:first-child {
        width: 189px;
      }

      &:nth-child(2) {
        width: 323px;
      }

      &:nth-child(3) {
        width: 323px;
      }
    }

  }

}

@media screen and (max-width: 1024px) {
  .about-me {
    margin-top: 35px;

    .about-text {
      z-index: 4;

      p {
        margin-left: 0;
      }
    }

    .about_img {
      img {
        width: 100%;
      }
    }
  }
}

@media screen and (max-width: 425px) {
  .about {
    height: auto;

    .about-me {
      flex-direction: column;
    }

    .about-text {
      margin: 50px 0;
    }

    .about_backg {
      p {
        font-size: 95px;
        line-height: 100px;
      }
    }
  }
}


