@import "./variables";

.question {
  height: 700px;
  padding-top: 110px;
  padding-bottom: 142px;
  overflow: hidden;
  position: relative;
  background: $bgrBlack;

  .question-cont {
    display: flex;
    justify-content: space-between;
  }
}

.question-items {
  z-index: 2;
  padding: 80px 53px 40px;
  background: rgba(60, 77, 73, 0.66);
  border-radius: 92px;
}

.question-item {
  &:not(:first-child) {
    margin-top: 21px;
  }
}

.active {
  p, div {
    color: $bgrLittle !important;

  }

}

.question-title {
  display: flex;
  align-items: center;


  span {
    font-family: $fontTitles;
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 26px;
    margin-right: 17px;
    text-align: center;
    color: rgba(209, 174, 126, 0.5);
  }

  p {
    flex: 1;
    cursor: pointer;
    font-family: $fontSupTitles;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 19px;
    text-align: left;
    color: #FFFFFF;

    &:hover {
      color: $bgrLittle;
      transition: all 0.5s;
    }
  }

  div {
    margin-left: 10px;
    font-family: $fontSupTitles;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    text-align: center;
    color: #FFFFFF;
  }
}

.question-words {
  position: absolute;
  top: 0;
  right: 0;

  h3 {
    font-family: $fontTitles;
    font-style: normal;
    font-weight: 400;
    font-size: 134px;
    line-height: 140px;
    text-align: center;
    color: rgba(209, 174, 126, 0.13);
  }
}

.question-content {
  display: flex;
  justify-content: center;
  font-family: $fontSupTitles;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  text-align: left;
  color: rgba(255, 255, 255, 0.47);


  //Animation
  animation: bck 0.5s cubic-bezier(0.455, 0.030, 0.515, 0.955) both;
  @keyframes bck {
    0% {
      transform: translateY(0) translateZ(-50px) rotateX(-90deg);

      transform-origin: top center;
    }
    100% {
      transform: translateY(0) translateZ(0) rotateX(0deg);
      transform-origin: top center;
    }
  }

  p {
    padding: 20px 0 20px;
    width: 350px;
  }
}

@media screen and (max-width: 425px) {
  .question {
    padding-top: 140px;
    height:auto;
  }
  .question-items {
    padding: 80px 27px 40px;

  }
  .question-content {
    p{
      width: 100%;
    }
  }
}
