@import "./variables";

.process {
  background: $bgrBlack;

  .process__items {
    padding: 53px 0 96px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(3, 1fr);
    grid-column-gap: 0;
    grid-row-gap: 52px;

  }

  .process__item {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin-right: 27px;
    grid-area: 1 / 1 / 2 / 2;

    span {
      font-family: $fontSupTitles;
      font-style: normal;
      font-weight: 400;
      font-size: 36px;
      line-height: 43px;
      color: #D1AE7E;
      text-align: right;
    }

    h3 {
      text-align: right;
      font-family: $fontSupTitles;
      font-style: normal;
      font-weight: 400;
      font-size: 24px;
      line-height: 29px;
      color: #FFFFFF;
      margin: 11px 0 11px;
    }

    p {
      width: 257px;
      font-family: $fontSupTitles;
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      line-height: 19px;
      text-align: right;
      color: #FFFFFF;
    }

    &:nth-child(2) {
      margin-right: 64px;
      grid-area: 2 / 1 / 3 / 2;

      p {
        width: 191px;
      }
    }

    &:nth-child(3) {
      margin-right: 27px;
      grid-area: 3 / 1 / 4 / 2;

      p {
        width: 286px;
      }
    }

    &:nth-child(4) {
      align-items: flex-start;
      margin: 0 0 0 27px;
      grid-area: 1 / 3 / 2 / 4;

      p {
        text-align: left;
        width: 275px;
      }
    }

    &:nth-child(5) {
      align-items: flex-start;
      margin: 0 0 0 64px;
      grid-area: 2 / 3 / 3 / 4;

      p {
        text-align: left;
        width: 275px;
      }
    }

    &:nth-child(6) {
      margin: 0 0 0 27px;
      align-items: flex-start;
      grid-area: 3 / 3 / 4 / 4;

      p {
        text-align: left;
        width: 275px;
      }
    }

    &:nth-child(7) {
      margin: 0;
      grid-area: 1 / 2 / 4 / 3;
    }
  }
}

@media screen and (max-width: 1024px) {
  .process {
    .process__item {
      margin-right: 17px;

      &:nth-child(2) {
        margin-right: 37px;
      }

      &:nth-child(3) {
        p {
          width: 209px;
        }
      }

      &:nth-child(4) {
        p {
          width: 242px;
        }
      }

      &:nth-child(4),
      &:nth-child(6) {
        margin-left: 17px;

      }

      &:nth-child(5) {
        margin-left: 37px;

        p {
          width: 224px;
        }
      }

      &:nth-child(6) {
        p {
          width: 200px;
        }
      }

      span {
        font-size: 33px;
      }

      h3 {
        font-size: 22px;
      }

      p {
        width: 242px;
      }

      img {
        width: 400px;
        margin-top: 25%;

      }
    }
  }
}

@media screen and (max-width: 768px) {
  .process {
    .process__items {
      padding: 50px 0 50px;
    }

    .process__item {
      margin-right: -30px;

      &:nth-child(2) {
        margin-right: 10px;
      }

      &:nth-child(3) {
        margin: -40px -30px 0 0;

        p {
          width: 170px;
        }
      }

      &:nth-child(4) {
        margin-left: -30px;

        p {
          width: 204px;
        }
      }

      &:nth-child(5) {
        margin-left: 10px;

        p {
          width: 155px;
        }
      }

      &:nth-child(6) {
        margin: -40px 0 0 -30px;

        p {
          width: 170px;
        }
      }

      span {
        font-size: 26px;
        line-height: 20px;
      }

      h3 {
        font-size: 20px;
      }

      p {
        width: 190px;
      }

      img {
        width: 290px;
        margin-top: 33%;

      }
    }
  }

}

@media screen and (max-width: 425px) {
  .process {
    .process__items {
      display: flex;
      flex-direction: column;
      align-items: center;

      .process__item {
        align-items: flex-start;
        margin: 0
      }

      p {
        width: 300px;
        font-size: 16px;
        text-align: left;

      }

      span {
        font-size: 36px;
        text-align: left;
        line-height: 50px;

      }

      h3 {
        font-size: 28px;
        text-align: left;

      }

      &:nth-child(2),
      &:nth-child(3),
      &:nth-child(4),
      &:nth-child(5),
      &:nth-child(6) {
        p {
          width: 300px;
        }
      }


      img {
        width: 400px;
        margin-top: 0;
      }

    }
  }
}



