@font-face {
    font-family: "Lighthaus";
    src: url("style/Lighthaus.otf") format("truetype");
    font-style: normal;
    font-weight: normal;
}
@font-face {
    font-family: "Book Antiqua";
    src: url("style/bookantiqua.ttf") format("truetype");
    font-style: normal;
    font-weight: normal;
}



* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

a {
    text-decoration: none;
}

ul, ol, li {
    list-style: none;
}

html,
body {
    height: 100%;
}
.title h2 {
    text-align: center;
    padding-top:55px;
    font-family: 'Lighthaus';
    font-style: normal;
    font-weight: 400;
    font-size: 48px;
    line-height: 62px;
    color: #FFFFFF;

}

#wrapper {
    display: flex;
    flex-direction: column;
    height: 100%;
}

main {
    flex: 1 0 auto;
}

footer {
    flex: 0 0 auto;
}

@media screen and (min-width: 1170px) {
    .container {
        max-width: 1200px;
        margin: 0 auto;
    }
}

@media screen and (max-width: 1024px) {
    .container {
        min-width: 800px;
        margin: 0 auto;
        padding: 0 15px 0;
    }
}

@media screen and (max-width: 768px) {
    .container {
        min-width: 420px;
        margin: 0 auto;
        padding: 0 15px 0;
    }
}

@media screen and (max-width: 425px) {
    .container {
        min-width: 390px;
        margin: 0 auto;
    }
}

@media screen and (max-width: 375px) {
    .container {
        min-width: 350px;
        margin: 0 auto;
    }
    .title h2 {

        font-size: 38px;

    }


}

@media screen and (max-width: 320px) {
        .container {
            min-width: 290px;
            margin: 0 auto;
        }
        .title h2 {

            font-size: 38px;

        }
    }

