@import "./variables";

.portfolio {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 96px;
  background: #3C4D49;


  .btn-portfolio {
    //Button
    display: flex;
    align-items: center;
    justify-content: flex-end;
    margin: 60px 0 0 850px;
    border: none;
    background: none;
    border-radius: 38.5px;
    padding: 15px 111px 15px 70px;
    transition: all 0.5s;
    cursor: pointer;

    //Text
    font-family: $fontSupTitles;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 43px;
    color: $bgrLittle;
    position: relative;

    span {
      &:before {
        position: absolute;
        content: '';
        width: 46px;
        height: 10px;
        top: 31px;
        right: 28px;
        border: 1px solid $bgrLittle;
        border-radius: 50%;
      }

      &:after {
        position: absolute;
        content: '';
        width: 33px;
        top: 36px;
        right: 53px;
        height: 0;
        background: $bgrLittle;
        border: 1px solid $bgrLittle;
        border-top-right-radius: 5px;
        border-bottom-right-radius: 5px;

      }
    }

    &:hover {
      transform: translateY(-5px);

      &:after {
        content: '';
        position: absolute;
        width: 5px;
        height: 5px;
        border-radius: 50%;
        box-shadow: 4px 4px 64px 19px rgba(209, 174, 126, 0.4);
        top: 50%;
        right: 50%;
      }
    }
  }
}

.portfolio-navigation {
  margin-top: 40px;

  .categories {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  //ul {
  //  display: flex;
  //  justify-content: space-between;
  //  align-items: center;
  //
  //li {
  //  list-style-type: none;

  .link {
    cursor: pointer;
    font-family: $fontTitles;
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 31px;
    color: #FFFFFF;
    text-decoration: none;
    transition: all 0.5s;

    &:focus {
      color: #000000
    }

    &:hover:not(.active) {
      color: $bgrLittle;
      transition: all 0.5s;
      position: relative;

      &:before {
        position: absolute;
        content: " ";
        background: $bgrLittle;
        width: 100%;
        height: 1px;
        bottom: -5px;
        animation: navlink 0.9s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
      }

      @keyframes navlink {
        0% {
          width: 0;
        }
        100% {
          width: 100%;
        }
      }

    }
  }

  .active {
    color: $bgrLittle;
    border-bottom: 1px solid $bgrLittle;
    padding-bottom: 5px;
  }
}


.all {
  margin-top: 65px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  position: relative;


  .all-top:nth-child(1) {
    margin-top: 97px;
    order: 0;
    flex: 0 1 auto;
    align-self: flex-end;
    transition: all 0.9s;
    border-radius: 200px 0 0 0;


  }

  .all-top:nth-child(2) {
    height: 389px;
    order: 0;
    flex: 0 1 auto;
    margin: 0 101px 0;
    align-self: flex-start;
  }

  .all-top:nth-child(3) {
    margin-top: 97px;
    order: 0;
    flex: 0 1 auto;
    align-self: flex-end;
    border-radius: 0 200px 0 0;


  }

  .all-top:nth-child(4) {
    margin-top: 54px;
    order: 0;
    flex: 0 1 auto;
    align-self: flex-start;
    border-radius: 0 0 0 200px;


  }

  .all-top:nth-child(5) {
    height: 389px;
    order: 0;
    flex: 0 1 auto;
    margin: 154px 101px 0;
    align-self: flex-end;
  }

  .all-top:nth-child(6) {
    margin-top: 54px;
    order: 0;
    flex: 0 1 auto;
    align-self: flex-start;
    border-radius: 0 0 200px 0;


  }

  .all-center {
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 469px;
    left: 440px;

    p {
      font-family: $fontTitles;
      width: 305px;
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      line-height: 18px;
      text-align: center;
      letter-spacing: 0.03em;
      color: #FFFFFF;

      &:last-child {
        margin-top: 30px;
      }
    }
  }


  .all-top {
    position: relative;
    overflow: hidden;
    animation: scale-in-center 1.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
    @keyframes scale-in-center {
      0% {
        transform: scale(0);
        opacity: 1;
      }
      100% {
        transform: scale(1);
        opacity: 1;
      }
    }

    img {

      transition: all 0.5s;
    }

    span {
      display: none;
      transition: all 0.9s;
      font-family: $fontTitles;
      font-style: normal;
      font-weight: 400;
      text-align: center;
      font-size: 24px;
      line-height: 31px;
      color: #fff;
    }


    &:hover {
      img {
        filter: brightness(0.3);
        transition: all 0.5s;
      }

      span {
        position: absolute;
        top: 114px;
        right: 5%;
        left: 5%;
        display: block;
        border-radius: 20px 0 20px 0;
        padding: 15px 0 15px 0;
        background: $bgrLittle;
        transition: all 1s;
        z-index: 30;
        animation: bottom 1s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;

        @keyframes bottom {
          0% {
            transform: translateY(0);
            opacity: 0;
          }
          100% {

            transform: translateY(50px);
            opacity: 1;
          }
        }
      }

    }

  }
}


//MEDIA
@media screen and (max-width: 1024px) {
  .portfolio {
    .btn-portfolio {
      margin: 60px 0 0;
    }
  }
  .all {
    .all-top {
      width: 30%;

      img {
        height: 391px;
      }

      &:nth-child(2) {
        margin: 0 25px 0 ;
      }

      &:nth-child(5) {
        margin: 154px 25px 0;
      }
    }

    .all-center {
      top: 450px;
      left: 349px;
    }
  }

}

@media screen and (max-width: 768px) {
  .portfolio-navigation {
    .link {
      font-size: 18px;
    }
  }
  .all {
    .all-top {
      img {
        width: 228px;
        height: 296px;
      }
    }

    .all-center {
      p {
        font-size: 14px;
        width: 226px;
      }

      top: 336px;
      left: 255px;
    }
  }
}

@media screen and (max-width: 425px) {
  .portfolio-navigation {
    .categories {
      flex-direction: column;

      div {
        .link {
          font-size: 21px;
          text-align: center;
          line-height: 50px;
        }
      }
    }
  }
  .all {
    flex-direction: column;
    align-items: center;

    .all-top {
      width: 100%;
      height: 100% !important;

      &:nth-child(2),
      &:nth-child(5){
        margin: 0;
      }

      &:nth-child(1),
      &:nth-child(3),
      &:nth-child(4),
      &:nth-child(6) {
        border-radius: 0;
      }

    }

    .all-top {
      margin-top: 5px !important;

      img {
        width: 100%;
        height: 100%;
      }
    }

    .all-center {
      display: none;
    }
  }
}


