@import "./variables";

.services {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-bottom: 150px;
  background: #3C4D49;


  .btn-services {
    //Button
    display: flex;
    align-items: center;
    justify-content: flex-end;
    margin: 60px 0 0 38%;
    border: none;
    background: none;
    border-radius: 38.5px;
    padding: 15px 111px 15px 70px;
    transition: all 0.5s;
    cursor: pointer;

    //Text
    font-family: $fontSupTitles;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 43px;
    color: $bgrLittle;
    position: relative;

    span {
      &:before {
        position: absolute;
        content: '';
        width: 46px;
        height: 12px;
        top: 31px;
        right: 28px;
        border: 1px solid $bgrLittle;
        border-radius: 50%;
      }

      &:after {
        position: absolute;
        content: '';
        width: 33px;
        top: 37px;
        right: 53px;
        height: 0;
        background: $bgrLittle;
        border: 1px solid $bgrLittle;
        border-top-right-radius: 5px;
        border-bottom-right-radius: 5px;

      }
    }

    &:hover {
      transform: translateY(-5px);

      &:after {
        content: '';
        position: absolute;
        width: 5px;
        height: 5px;
        border-radius: 50%;
        box-shadow: 4px 4px 64px 19px rgba(209, 174, 126, 0.4);
        top: 50%;
        right: 50%;
      }
    }
  }
}

.service-items {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 94px;

}

.service-item {
  background: #343736;
  border-radius: 200px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 87px 38px 41px;
  &:nth-child(2) {
    margin: 0 25px 0;
    h3{
      width: 180px;
    }
  }

  h3 {
    width: 216px;
    margin-bottom: 19px;
    font-family: $fontSupTitles;
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 24px;
    text-align: center;
    color: #D1AE7E;
  }

  p {
    width: 292px;
    font-family: $fontSupTitles;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 35px;
    text-align: center;
    color: #FFFFFF;
  }

  span {
    margin-top: 60px;
    position: relative;
    font-family: $fontSupTitles;
    font-style: normal;
    font-weight: 400;
    font-size: 32px;
    line-height: 39px;
    text-align: center;
    color: #FFFFFF;

    &:before {
      top: -29px;
      left: 25%;
      position: absolute;
      content: " ";
      width: 50px;
      height: 1px;
      background-color: #FFFFFF;
      transform: rotate(90deg);
    }
  }
}

@media screen and (max-width: 1024px) {
  .services {
    padding-bottom: 50px;

    .btn-services {
      margin: 30px 0 0 35%;
    }
  }

  .service-item {
    padding: 40px 10px 40px;

    &:nth-child(2) {
      margin: 0 5px 0;

      h3 {
        width: 180px;
      }
    }

  }
}

@media screen and (max-width: 768px) {
  .services {
    padding-bottom: 50px;

    .btn-services {
      margin: 30px 0 0 31%;
    }
  }
  .service-item {
    padding: 40px 10px 30px;

    &:nth-child(2) {
      h3 {
        width: 80px;
      }
    }

    h3 {
      width: 180px;
      font-size: 16px;
    }

    p {
      width: 214px;
      font-size: 11px;
      line-height: 20px;
    }

    span {
      font-size: 22px;

      &:before {
        width: 35px;
        left: 24%;
      }
    }
  }
}

@media screen and (max-width: 425px) {
  .service-items {
    flex-direction: column;
  }
  .services {
    padding-bottom: 50px;

    .btn-services {
      margin: 30px 0 0 12%;
    }
  }
  .service-item {
    padding: 87px 38px 41px;

    &:nth-child(2) {
      margin: 15px 0 15px;

      h3 {
        width: 136px;
      }
    }

    h3 {
      width: 216px;
      font-size: 20px;
    }

    p {
      width: 214px;
      font-size: 14px;
      line-height: 25px;
    }

    span {
      font-size: 32px;

      &:before {
        width: 50px;
        left: 24%;
      }
    }
  }
}
