
//COLOR
$bgrGreen: #3C4D49;
$bgrBlack: #343736;
$bgrLittle: #d1ae7e;

//FONT
$fontTitles: 'Lighthaus';
$fontSupTitles: 'Book Antiqua';

//Title
@mixin title{
  text-align: center;
  padding-top:55px;
  font-family: $fontTitles;
  font-style: normal;
  font-weight: 400;
  font-size: 48px;
  line-height: 62px;
  color: #FFFFFF;
}

//input
@mixin input{
  width: 314px;
  height: 58px;
  background: #343736;
  border-radius: 50px;
}