@import "./variables";


header {
  padding: 26px 0 26px;
  background: $bgrGreen;
  display: flex;
  flex-direction: column;
  position: relative;


  &:before {
    position: absolute;
    top: 0;
    right: 0;
    width: 50%;
    height: 100%;
    content: " ";
    background: url('../../public/img/title.png') center center;
    background-repeat: no-repeat;
    background-size: cover;

  }

}

.header_menu {
  z-index: 4;
  display: flex;
  align-items: center;
  justify-content: space-between;

  svg {
    display: none;
  }
}

.logo {
  //margin-right: 500px;
  .logo-header {
    font-family: $fontSupTitles;
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 29px;
    color: #FFFFFF;

  }
}

nav {
  .link-header {
    text-decoration: none;
    cursor: pointer;
    font-family: $fontSupTitles;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    color: #FFFFFF;
    transition: all 0.5s;
    position: relative;

    &:hover {
      color: $bgrLittle;
      transition: all 0.5s;

      &:before {
        position: absolute;
        content: '';
        width: 100%;
        height: 1px;
        background: $bgrLittle;
        bottom: -2px;
        z-index: 5;
        animation: border 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
      }

      @keyframes border {
        0% {
          width: 0;
        }
        100% {
          width: 100%;
        }
      }

      &:after {
        content: '';
        position: absolute;
        width: 5px;
        height: 5px;
        border-radius: 50%;
        box-shadow: 4px 4px 64px 19px rgba(209, 174, 126, 0.2);
        top: 50%;
        right: 50%;
      }
    }

  }

  .link-header:not(:first-child) {
    margin-left: 45px;
  }

}

.header_content {
  z-index: 10;

  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 168px;

  h1 {
    text-align: center;
    font-family: $fontTitles;
    font-style: normal;
    font-weight: 400;
    font-size: 128px;
    line-height: 165px;
    color: #FFFFFF;
    animation: title 3.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;

  }


  h3 {
    text-align: center;
    font-family: $fontSupTitles;
    font-style: normal;
    font-weight: 400;
    font-size: 40px;
    line-height: 58px;
    color: rgba(255, 255, 255, 0.6);
    animation: title 3.8s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
  }

  @keyframes title {
    0% {
      opacity: 0;
      transform: translateY(-300px);
    }
    100% {
      opacity: 1;
      transform: translateY(0);
    }
  }

  .btn-header {
    //Button
    margin-top: 60px;
    background: rgba(60, 77, 73, 0.62);
    border: 1px solid #FFFFFF;
    border-radius: 38.5px;
    padding: 15px 111px 15px 70px;
    margin-bottom: 240px;
    transition: all 0.5s;
    cursor: pointer;
    animation: btn 3.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
    @keyframes btn {
      0% {
        opacity: 0;
        transform: translateY(300px);
      }
      100% {
        opacity: 1;
        transform: translateY(0);
      }
    }

    //Text
    font-family: $fontSupTitles;
    font-style: normal;
    font-weight: 400;
    font-size: 26px;
    line-height: 43px;
    color: $bgrLittle;

    position: relative;

    span {
      width: 2px;
      height: 2px;
      background: $bgrLittle;

      &:before {
        position: absolute;
        content: '';
        width: 46px;
        height: 12px;
        top: 31px;
        right: 28px;
        border: 1px solid $bgrLittle;
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
        border-radius: 50%;
      }

      &:after {
        position: absolute;
        content: '';
        width: 33px;
        top: 37px;
        right: 53px;
        height: 0;
        background: $bgrLittle;
        border: 1px solid $bgrLittle;
        border-top-right-radius: 5px;
        border-bottom-right-radius: 5px;

      }
    }

    &:hover {
      transition: all 0.5s;
      transform: translateY(-5px);
      box-shadow: 4px 4px 83px 19px rgba(209, 174, 126, 0.2);
    }
  }
}

@media screen and (max-width: 1024px) {
  .modal-content {
    width: 954px;
  }
}

@media screen and (max-width: 768px) {
  .header_content h1 {
    font-size: 100px;
    line-height: 120px;
  }
  .header_content {
    padding-top: 138px;

    .btn-header {
      padding: 10px 100px 10px 70px;
      font-size: 24px;
    }

    .btn-header {
      span {
        &:before {
          top: 27px;
        }

        &:after {
          top: 33px;

        }
      }
    }
  }

}

@media screen and (max-width: 425px) {
  .header_content {
    h1 {
      font-size: 56px;
      line-height: 100px;
    }

    .btn-header {
      padding: 5px 60px 5px 30px;
      font-size: 14px;

      span {
        &:before {
          top: 22px;
          width: 30px;
          height: 9px;
          right: 15px;
        }

        &:after {
          top: 27px;
          width: 24px;
          right: 32px;

        }
      }
    }

  }
  .header_menu {
    svg {

      display: block;
      z-index: 2;

      path {
        fill: #FFFFFF;

      }
    }
  }
  .navigator {
    display: none;
    position: fixed;
    width: 90%;
    height: 100vh;
    background: $bgrGreen;
    left: 0;
    top: 0;
    z-index: 1;
    overflow: hidden;
    animation: slide-right 0.9s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
  }
  .show {
    display: flex;
    flex-direction: column;
    align-items: center;

    .link-header {
      text-decoration: none;
      cursor: pointer;
      font-family: $fontTitles;
      font-style: normal;
      font-weight: 400;
      font-size: 24px;
      line-height: 17px;
      color: #FFFFFF;
      transition: all 0.5s;

      &:first-child {
        margin-top: 45%;
      }

      &:not(:first-child) {
        margin: 55px 0 0;
      }

    }
  }
  @keyframes slide-right {
    0% {
      -webkit-transform: translateZ(-1400px) translateX(1000px);
      transform: translateZ(-1400px) translateX(1000px);
      opacity: 0;
    }
    100% {
      -webkit-transform: translateZ(0) translateX(0);
      transform: translateZ(0) translateX(0);
      opacity: 1;
    }
  }
  .nav_mobile {
    z-index: 4;
    position: fixed;
    width: 15%;
    height: 100vh;
    top: 0;
    right: 0;
    background: $bgrGreen;
    background: #FFFFFF;

    .close {
      margin-top: 40px;
      margin-left: 19px;

      path {
        fill: #3C4D49;
      }
    }

  }
  nav {
    .header_content h1 {
      font-size: 68px;
      line-height: 100px;
    }

    .header_content {
      padding-top: 168px;

      .btn-header {
        padding: 10px 60px 10px 30px;
        font-size: 18px;
      }

      .btn-header {
        span {
          &:before {
            top: 27px;
            width: 30px;
            height: 9px;
            right: 12px;
          }

          &:after {
            top: 31px;
            width: 26px;
            right: 25px;

          }
        }
      }
    }
  }

}

@media screen and (max-width: 375px) {
  .header_content h1 {
    font-size: 56px;
    line-height: 100px;
  }
}

@media screen and (max-width: 320px) {
  .modal-content {
    width: 300px;
    height: 378.26px;
  }
  .header_content {
    h1 {
      font-size: 50px;
    }
    h3{
        font-size: 31px;
    }
  }
}


