@import "./variables";
footer{
  background: $bgrGreen;
}
.footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 58px;

  h2 {
    text-align: center;
    padding-top: 145px;
    font-family: $fontTitles;
    font-style: normal;
    font-weight: 400;
    font-size: 48px;
    line-height: 62px;
    color: #FFFFFF;
  }

  form {
    .input__frm {
      display: flex;
      margin-top: 46px;
      justify-content: center;
    }

    .btn_mdl {
      margin-top: 37px !important;
    }
  }

  .footer-social {
    position: relative;
    padding: 81px 0 58px;

    a {
      position: relative;

      padding: 49px 20px 49px;
      width: 116px;
      height: 97px;
      text-decoration: none;
      color: #fff;
      border-radius: 50%;
      background: #343736;

      &:nth-child(even) {
        margin: 0 36px 0;
      }
    }

    &::before {
      position: absolute;
      content: " ";
      top: 35%;
      right: -5%;
      width: 516px;
      height: 63px;
      border: 1px solid #FFFFFF;
      border-radius: 50%;

    }
  }
}

@media screen and (max-width: 425px) {
  .footer {
    .footer-social {
      display: flex;
      flex-direction: column;
      &:before{
        transform:rotate(90deg);
        top: 47%;
        right: -136%;
      }
      a{
        text-align: center;
        width: 100%;
        height: 100%;
        &:nth-child(even) {
          margin: 15px 0 15px;
        }
      }
    }
  }
}
