@import "src/style/variables";

.modal {
  height: 100%;
  width: 100%;
  background: rgba(54, 54, 54, 0.75);
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  pointer-events: none;
  transition: 0.5s;
  z-index: 999999;

}

.modal.active {
  pointer-events: all;
  opacity: 1;
  z-index: 1000;
}

.modal-content {
  border-radius: 0 204px 0 204px;
  background: rgb(60, 77, 73);
  width: 1054px;
  height: 478.26px;
  overflow: auto;
  z-index: 100;
  display: flex;
  flex-direction: column;
  position: relative;
}

.modal-form {

  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  top: 98px;
  right: 161px;
  left: 161px;

  h2 {
    font-family: $fontTitles;
    font-style: normal;
    font-weight: 400;
    font-size: 48px;
    line-height: 62px;
    color: #ffffff;
  }
}

.modal__items {
  display: flex;
  justify-content: space-between;

  .modal_item {
    background-size: cover;
    background: url("../../../public/img/5.5.png") no-repeat center center;
    width: 320px;
    height: 478.26px;
    border-radius: 0 0 0 198px;

    &:nth-child(even) {
      float: right;
      border-radius: 0 198px 0 0;
      background-size: cover;
      background: url("../../../public/img/5.png") no-repeat center center;
    }
  }
}
@media screen and (max-width: 1024px) {
  .modal-content {
    width: 700px;
    height: 378.26px;
  }
  .modal__items .modal_item {
    width: 220px;
    height: 378.26px;
  }
  .modal-form h2 {
    font-size: 29px;
  }
}

@media screen and (max-width: 768px) {

  .modal-content {
    width: 700px;
    height: 378.26px;
  }
  .modal__items .modal_item {
    width: 220px;
    height: 378.26px;
  }
  .modal-form h2 {
    font-size: 29px;
  }
}
@media screen and (max-width: 425px) {

  .modal-content {
    width: 350px;
    height: 378.26px;
  }
  .modal__items .modal_item {
    width: 220px;
    height: 378.26px;
  }
  .modal-form h2 {
    width: 300px;
    text-align: center;
    font-size: 28px;
    line-height: 35px;
  }
}


@media screen and (max-width: 320px) {
  .modal-content {
    width: 300px;
    height: 378.26px;
  }
  .modal-form h2 {
    width: 267px;
    font-size: 22px;
  }
}




